<template>
  <div>
    <el-table
      ref="multipleTable"
      :data="options"
      stripe
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange"
      @sort-change="sortFilterFun"
    >
      <el-table-column type="selection" width="55"> </el-table-column>
      <el-table-column fixed prop="gb_id" label="全选/ID" width="150"> </el-table-column>
      <el-table-column prop="title" label="名称" width="300">
        <template slot-scope="scope">
          <div class="imgnav">
            <el-image
              class="groupBuyImg"
              style="width: 50px; height: 50px"
              :src="scope.row.image"
              fit="fill"
            ></el-image>
            <span class="title-s">{{ scope.row.title }}</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="goods_detail" label="套餐组合" width="400">
        <template slot-scope="scope">
          <span v-for="(item, k) in scope.row.goods_detail" :key="k"
            >{{ item.goods_name }}，{{ item.goods_num }}，{{ item.goods_price / 100 }}，{{
              item.goods_unit
            }},</span
          >
        </template>
      </el-table-column>
      <el-table-column prop="total_price" sortable="custom" label="价格" width="90">
        <template slot-scope="scope">
          <div class="yellow-t">{{ scope.row.total_price / 100 }}</div>
        </template>
      </el-table-column>
      <el-table-column prop="sales" sortable="custom" label="近30天销售" width="150">
        <template slot-scope="scope">
          <div class="green-t">{{ scope.row.sales }}</div>
        </template>
      </el-table-column>
      <el-table-column prop="create_time" sortable="custom" label="发布时间" width="180">
      </el-table-column>
      <el-table-column prop="expire_time" sortable="custom" label="到期时间">
      </el-table-column>
      <el-table-column fixed="right" label="操作" width="150">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small"
            >查看/编辑</el-button
          >
          <el-button @click="delClick(scope.row)" type="text" size="small"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "goodsItem",
  props: {
    options: Array,
    merchants: Object,
  },
  data() {
    return {
      sortVal: "",
      sortStatus: false,
      order: "asc",
      // statusArr:[{text:'下架', value:'0'},{text:'售完', value:'1'},{text:'正常', value:'2'},{text:'删除', value:'3'}],
      multipleSelection: "",
      nameArr: [],
    };
  },
  mounted() {},
  methods: {
    //table筛选
    filterTagTable(filters) {
      this.filterMethod(filters);
    },
    //table筛选
    filterMethod(val) {
      var key = [];
      var value = [];
      for (var i in val) {
        key.push(i);
        value.push(val[i]);
      }
      key = key.join(",");
      value = value.join(",");
      //接口逻辑
      this.f_status = value;
      this.$emit("childFun", this.f_status, "", key, 2);
      // if(key=='status' && value){
      //     this.filterGetGoodsList()
      // }else{
      //     this.getGoodsList()
      // }
    },
    // filterGetGoodsList() {
    //     this.pageSign = 2
    //     let data = {page: this.page, status: this.f_status, class_id:this.class_id}
    //     this.$store.dispatch('goods/getGoodsList',data).then((res) => {
    //         this.options = res.data
    //         this.count = res.count
    //     })
    // },
    //table排序
    sortMethod(val) {
      this.sortVal = val;
      this.sortStatus = !this.sortStatus;
      if (this.sortStatus) {
        this.order = "asc"; //正序
      } else {
        this.order = "desc"; //倒序
      }
      //接口逻辑
      // this.sortDataFun()
      this.$emit("childFun", this.sortVal, this.order, "", 1);
    },
    //table排序
    sortFilterFun(column) {
      this.sortMethod(column.prop);
    },
    //批量删除操作
    handleSelectionChange(val) {
      var arr = [];
      for (var i = 0; i < val.length; i++) {
        arr.push(val[i].gb_id);
        this.nameArr.push(val[i].title);
      }
      this.multipleSelection = arr;
      // arr = Array.from(new Set(arr))
      this.nameArr = Array.from(new Set(this.nameArr));
      // this.multipleSelection = arr.join(',')
      this.$emit("childFun", this.multipleSelection, "", this.nameArr, 4);
    },
    //查看、修改商品
    handleClick(row) {
      this.$emit("edit", row, false);
      // this.$router.push({
      //   path: "/editGroupBuy",
      //   query: {
      //     gb_id: row.gb_id,
      //     merchant_id: this.merchants.merchant_id,
      //     shop_id: this.merchants.shop_id,
      //   },
      // });
    },
    // 删除单个
    delClick(row) {
      this.$emit("childFun", row.gb_id, true, "删除-" + row.title, 3);
    },
  },
};
</script>

<style scoped></style>
