<template>
  <div>
    <div class="shopSet" v-if="!showRuleBox">
      <el-row :gutter="20">
        <el-col :span="4">
          <el-card
            ><div class="goods-class-list-left" @click="addNewPlaceType">
              <i class="el-icon-plus">新建分类</i>
            </div>
          </el-card>

          <el-card :shadow="type_id == 0 ? 'never' : 'always'"
            ><div
              :class="
                'goods-class-list-left' +
                (type_id == 0 ? ' goods-class-list-selected' : '')
              "
              @click="tabFun(0)"
            >
              <i class="el-icon-folder-opened">显示全部</i>
            </div>
          </el-card>

          <draggable
            class="list-group"
            v-model="placeTypeData"
            :options="{ animation: 20 }"
            @change="draggableChangeClass"
          >
            <el-card
              v-for="(item, index) in placeTypeData"
              :key="'class' + index"
              :shadow="type_id == item.type_id ? 'never' : 'always'"
            >
              <div
                @click="tabFun(item.type_id)"
                :class="
                  'goods-class-list-left' +
                  (type_id == item.type_id ? ' goods-class-list-selected' : '')
                "
              >
                {{ item.name }}
              </div>
              <span class="goods-class-list-right" v-if="item.status == 1">
                <el-button
                  type="warning"
                  size="mini"
                  @click="changeStatusClass(item, false)"
                  >禁用</el-button
                >
              </span>
              <span class="goods-class-list-right" v-if="item.status == 0">
                <el-button type="" size="mini" @click="editClass(item)"
                  >编辑</el-button
                >
                <el-button
                  type=""
                  size="mini"
                  @click="changeStatusClass(item, true)"
                  >启用</el-button
                >
              </span>
            </el-card>
          </draggable>
        </el-col>
        <el-col :span="20">
          <div class="add-box">
            <div v-if="!is_delete" class="btn-nav">
              <el-button type="primary" @click="handleNewPlace"
                >新增座位</el-button
              >
              <el-button
                type="primary"
                :disabled="!multipleSelection.length > 0"
                @click="handleDeletePlace"
                >删除</el-button
              >
              <el-button type="warning" @click="handleDeleteBox(true)" plain
                >回收站</el-button
              >
              <el-button type="warning" plain @click="handleRuleBox"
                >座位规则</el-button
              >
            </div>
            <div v-if="is_delete" class="btn-nav">
              <el-button
                type="primary"
                @click="handleRecoveryPlace"
                :disabled="!multipleSelection.length > 0"
                >还原</el-button
              >
              <el-button type="warning" plain @click="handleDeleteBox(false)"
                >返回</el-button
              >
            </div>
            <div class="so-nav">
              <el-input
                class="so-ipt"
                placeholder="请输入座位名称"
                suffix-icon="el-icon-search"
                v-model="soVal"
                :clearable="true"
                @change="getAllPlace"
              >
              </el-input>
              <el-button class="sosuo" type="primary" @click="getAllPlace"
                >搜索</el-button
              >
            </div>
          </div>
          <Place-Area
            :merchants="merchants"
            @selectedArea="selectedArea"
            @getPlaceAreaData="getPlaceAreaData"
          />
          <Place-Item
            :merchants="merchants"
            :options="placListData"
            :is-delete="is_delete"
            @editPlace="handleEditPlace"
            @deleteFun="deletePlace"
            @recoveryFun="recoveryPlace"
            @sortFun="sortPlace"
          />
          <div class="pagebox">
            <el-pagination
              ref="pagination"
              :hide-on-single-page="value"
              background
              layout="prev, pager, next"
              :total="count"
              @current-change="pageChange"
            >
            </el-pagination>
          </div>
        </el-col>
      </el-row>

      <el-dialog
        :title="placeForm.type_id == 0 ? '新建座位' : '编辑座位'"
        :visible.sync="placePlaceDialog"
        destroy-on-close
        :close-on-press-escape="false"
        :close-on-click-modal="false"
      >
        <el-form
          :model="placeForm"
          ref="placeForm"
          label-width="80px"
          class="addType"
          :rules="placeRules"
        >
          <el-form-item label="名称" prop="place_name">
            <el-input v-model="placeForm.place_name" />
          </el-form-item>
          <el-form-item label="分类" prop="type_id">
            <el-select v-model="placeForm.type_id" placeholder="请选择">
              <el-option
                v-for="item in placeTypeData"
                :key="item.type_id"
                :label="item.name"
                :value="item.type_id"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="区域" prop="area_id">
            <el-select v-model="placeForm.area_id" placeholder="请选择">
              <el-option
                v-for="item in placeAreaData"
                :key="item.area_id"
                :label="item.area_name"
                :value="item.area_id"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="最少人数" prop="min_people_num">
            <el-input v-model.number="placeForm.min_people_num" />
          </el-form-item>
          <el-form-item label="最多人数" prop="max_people_num">
            <el-input v-model.number="placeForm.max_people_num" />
          </el-form-item>
          <el-form-item label="低消" prop="basic_price">
            <el-input v-model.number="placeForm.basic_price" />
            <div>注：若座位单独设置低消，一座位低消为准。</div>
          </el-form-item>
          <el-form-item label="订金" prop="deposit">
            <el-input v-model.number="placeForm.deposit" />
            <div>注：若座位单独设置订金，一座位订金为准。</div>
          </el-form-item>

          <el-form-item label="规则" prop="rule_id">
            <el-select v-model="placeForm.rule_id" placeholder="请选择">
              <el-option
                v-for="item in placeRuleData"
                :key="item.rule_id"
                :label="item.name"
                :value="item.rule_id"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="排序" prop="sort">
            <el-input v-model.number="placeForm.place_sort" />
          </el-form-item>
          <el-form-item label="是否启用">
            <el-switch
              v-model="placeForm.place_status"
              :active-value="1"
              :inactive-value="0"
            >
            </el-switch>
          </el-form-item>
          <div style="text-align: right">
            <el-button type="" @click="placePlaceDialog = false"
              >取消</el-button
            >
            <el-button type="primary" @click="submitPlace" :loading="isLoading"
              >保存</el-button
            >
          </div>
        </el-form>
      </el-dialog>

      <el-dialog
        :title="typeForm.type_id == 0 ? '新建分类' : '编辑分类'"
        :visible.sync="placeDialog"
        destroy-on-close
        :close-on-click-modal="false"
      >
        <el-form
          :model="typeForm"
          ref="typeForm"
          label-width="80px"
          class="addType"
          :rules="typeRules"
        >
          <el-form-item label="类型名称" prop="name">
            <el-input v-model="typeForm.name" />
          </el-form-item>
          <el-form-item label="最少人数" prop="min_people_num">
            <el-input v-model.number="typeForm.min_people_num" />
          </el-form-item>
          <el-form-item label="最多人数" prop="max_people_num">
            <el-input v-model.number="typeForm.max_people_num" />
          </el-form-item>
          <el-form-item label="低消" prop="basic_price">
            <el-input v-model.number="typeForm.basic_price" />
          </el-form-item>
          <el-form-item label="订金" prop="deposit">
            <el-input v-model.number="typeForm.deposit" />
          </el-form-item>
          <el-form-item label="排序" prop="sort">
            <el-input v-model.number="typeForm.sort" />
          </el-form-item>
          <el-form-item label="是否启用">
            <el-switch
              v-model="typeForm.status"
              :active-value="1"
              :inactive-value="0"
            >
            </el-switch>
          </el-form-item>
          <div style="text-align: right">
            <el-button
              v-if="typeForm.type_id > 0"
              type="danger"
              @click="deleteType"
              >删除</el-button
            >
            <el-button @click="placeDialog = false">取消</el-button>
            <el-button type="primary" @click="submitType" :loading="isLoading"
              >保存</el-button
            >
          </div>
        </el-form>
      </el-dialog>
    </div>
    <placeRule
      :merchants="merchants"
      v-if="showRuleBox && enable"
      @handleRuleBox="handleRuleBox"
    ></placeRule>
  </div>
</template>
<script>
import PlaceArea from "./PlaceArea.vue";
import PlaceItem from "./placeItem.vue";
import placeRule from "@/views/shopset/PlaceRuleindex";
import _ from "lodash";
export default {
  name: "PlaceMain",
  components: {
    PlaceArea,
    PlaceItem,
    placeRule,
  },
  data() {
    return {
      isLoading: false,
      placeDialog: false,
      placePlaceDialog: false,
      showRuleBox: false,
      soVal: "",
      options: [],
      multipleSelection: [],
      value: true,
      page: 1,
      count: 0,
      detaileData: [],
      title: "",
      gb_id: 0,
      placeFormType: "",
      placeFormArea: "",
      placeFormRule: "",
      placeAreaData: [],
      is_delete: false,
      //排序枚举
      sortableList: {
        ascending: "asc",
        descending: "desc",
        asc: "ascending",
        desc: "descending",
      },
      order: "",
      sort: "",
      typeRules: {
        name: [{ required: true, message: "请输入分类名称", trigger: "blur" }],
        min_people_num: [
          { required: true, message: "请输入最少人数", trigger: "blur" },
          { type: "number", message: "必须为数字值" },
        ],
        max_people_num: [
          { required: true, message: "请输入最大人数", trigger: "blur" },
          { type: "number", message: "必须为数字值" },
        ],
        basic_price: [
          { required: true, message: "必须设置抵消数", trigger: "blur" },
          { type: "number", message: "必须为数字值" },
        ],
        deposit: [
          { required: true, message: "必须设置订金数", trigger: "blur" },
          { type: "number", message: "必须为数字值" },
        ],
        sort: [
          { required: true, message: "必须排序", trigger: "blur" },
          { type: "number", message: "必须为数字值" },
        ],
      },
      placeRules: {
        place_name: [
          { required: true, message: "请输入名称", trigger: "blur" },
        ],
        type_id: [{ required: true, message: "请选择分类", trigger: "blur" }],
        area_id: [{ required: true, message: "请选择区域", trigger: "blur" }],
        min_people_num: [
          { required: true, message: "请输入最少人数", trigger: "blur" },
          { type: "number", message: "必须为数字值" },
        ],
        max_people_num: [
          { required: true, message: "请输入最大人数", trigger: "blur" },
          { type: "number", message: "必须为数字值" },
        ],
        basic_price: [
          { required: true, message: "必须设置抵消数", trigger: "blur" },
          { type: "number", message: "必须为数字值" },
        ],
        deposit: [
          { required: true, message: "必须设置订金数", trigger: "blur" },
          { type: "number", message: "必须为数字值" },
        ],
      },
      placeRuleData: [],
      placeForm: {
        place_id: 0,
        merchant_id: 0,
        shop_id: 0,
        type_id: null,
        area_id: null,
        place_name: "",
        place_desc: "",
        min_people_num: null,
        max_people_num: null,
        basic_price: null,
        deposit: null,
        rule_id: null,
        place_sort: 0,
        place_status: 1,
        place_desc: "",
      },
      placListData: [
        {
          place_id: 0,
          merchant_id: 0,
          shop_id: 0,
          type_id: 0,
          type_name: "",
          area_id: 0,
          area_name: "",
          place_name: "",
          place_desc: "",
          min_people_num: 0,
          max_people_num: 0,
          basic_price: 0,
          deposit: 0,
          rule_id: 0,
          rule_name: "",
          place_sort: 0,
          place_status: 0,
          place_desc: "",
        },
      ],
      placeTypeData: [
        {
          type_id: "",
          name: "",
          sort: 0,
          min_people_num: "",
          max_people_num: "",
          basic_price: "",
          deposit: "",
        },
      ],
      type_id: 0,
      area_id: "",
      editableTabsValue: "0",
      tabStatus: "0",
      typeForm: {
        type_id: 0,
        name: "",
        ename: "",
        min_people_num: "",
        max_people_num: "",
        basic_price: "",
        deposit: "",
        sort: 0,
        status: 1,
      },
    };
  },
  props: {
    merchants: Object,
    enable: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    draggableChangeClass(obj) {
      let classSort = [];
      for (let i in this.placeTypeData) {
        this.placeTypeData[i].sort = this.placeTypeData.length - 1 - i;
        classSort.push({
          type_id: this.placeTypeData[i].type_id,
          sort: Number(this.placeTypeData[i].sort),
        });
      }
      let datas = {
        merchant_id: this.merchants.merchant_id,
        shop_id: this.merchants.shop_id,
        place_type: classSort,
      };
      this.api.shop.placetypeSetSort(datas).then(({ data }) => {
        let res = data.data;
        this.getPlaceType();
      });
    },
    handleNewPlace() {
      this.placeForm = {
        place_id: 0,
        merchant_id: 0,
        shop_id: 0,
        type_id: null,
        area_id: null,
        place_name: "",
        place_desc: "",
        min_people_num: null,
        max_people_num: null,
        basic_price: null,
        deposit: null,
        rule_id: null,
        place_sort: 0,
        place_status: 1,
        place_desc: "",
      };
      this.isLoading = false;

      this.placePlaceDialog = true;
    },
    handleEditPlace(row) {
      console.info(row);

      this.placeForm = row;
      this.placeForm.type_id = String(row.type_id);
      this.isLoading = false;
      this.placePlaceDialog = true;
    },
    //打开规则界面
    handleRuleBox() {
      this.showRuleBox = !this.showRuleBox;
    },
    changePlaceType(v) {
      this.placeFormType = v;
    },
    changePlaceArea(v) {
      this.placeFormArea = v;
    },
    changePlaceRule(v) {
      this.placeFormRule = v;
    },
    editClass(obj) {
      this.isLoading = false;
      this.typeForm = Object.assign({}, obj);
      delete this.typeForm.icon;
      delete this.typeForm.place_count;
      this.typeForm.basic_price = this.CalculateData(
        this.typeForm.basic_price,
        "/"
      );
      this.typeForm.deposit = this.CalculateData(this.typeForm.deposit, "/");
      console.info(this.typeForm);
      this.placeDialog = true;
    },
    selectedArea(obj) {
      this.area_id = Number(obj.area_id);
      this.getAllPlace();
    },
    tabFun(type_id) {
      this.page = 1;
      this.type_id = Number(type_id);
      this.getAllPlace();
    },
    addNewPlaceType() {
      this.typeForm = {
        type_id: 0,
        name: "",
        ename: "",
        min_people_num: "",
        max_people_num: "",
        basic_price: "",
        deposit: "",
        sort: 0,
        status: 1,
      };
      this.placeDialog = true;
    },
    getAllPlace() {
      const datas = {
        page: this.page,
        type_id: this.type_id,
        merchant_id: this.merchants.merchant_id,
        shop_id: this.merchants.shop_id,
        area_id: this.area_id,
        sort: this.sort,
        order: this.order,
        place_name: this.soVal,
      };
      if (Number(this.type_id) == 0) {
        delete datas.type_id;
      }
      if (Number(this.area_id) == 0) {
        delete datas.area_id;
      }
      if (this.is_delete) {
        datas.is_delete = 1;
      } else {
        delete datas.is_delete;
      }
      //this.$store.dispatch("goods/getGroupBuyList", datas).then((res) => {
      this.api.shop.placeList(datas).then(({ data }) => {
        let res = data;
        this.placListData = res.data;
        this.count = res.count;
        this.$refs.pagination.internalCurrentPage = this.page;
        if (this.placListData) {
          //计算金额
          for (var i = 0; i < this.placListData.length; i++) {
            this.placListData[i].basic_price = this.CalculateData(
              this.placListData[i].basic_price,
              "/"
            );
            this.placListData[i].deposit = this.CalculateData(
              this.placListData[i].deposit,
              "/"
            );
          }
        }
      });
    },
    // 获取座位类型列表
    getPlaceType(fun) {
      const datas = {
        page_size: 10000,
        merchant_id: this.merchants.merchant_id,
        shop_id: this.merchants.shop_id,
      };
      //this.$store.dispatch("shop/getPlaceType", datas).then((res) => {
      this.api.shop.getPlaceType(datas).then(({ data }) => {
        let res = data;
        this.placeTypeData = res.data;
        // this.placeTypeData.forEach((item, index) => {
        //   if (index == 0) {
        //     this.type_id = item.type_id;
        //     this.editableTabsValue = item.type_id + "";
        //   }
        //   item.type_id = String(item.type_id);
        //   // let obj = item
        //   // obj.name = index + 1 + ''
        // });
        if (fun) fun();
      });
    },
    //提交座位
    submitPlace() {
      this.$refs["placeForm"].validate((valid) => {
        if (valid) {
          let datas = _.cloneDeep(this.placeForm);
          delete datas.operator_id;
          delete datas.type_name;
          delete datas.area_name;
          delete datas.rule_name;
          datas.type_id = Number(this.placeForm.type_id);
          datas.min_people_num = Number(datas.min_people_num);
          datas.max_people_num = Number(datas.max_people_num);
          datas.basic_price = this.CalculateData(datas.basic_price, "*");
          datas.deposit = this.CalculateData(datas.deposit, "*");
          datas.merchant_id = Number(this.merchants.merchant_id);
          datas.shop_id = Number(this.merchants.shop_id);
          console.info(datas);
          this.isLoading = true;
          if (datas.place_id == 0) {
            delete datas.place_id;
            this.$store
              .dispatch("shop/addPlaceList", datas)
              .then((res) => {
                this.isLoading = false;
                this.$message({
                  message: res.msg,
                  type: res.code == 0 ? "success" : "error",
                });
                this.reloadData();
              })
              .catch((e) => {
                console.error(e);
                this.isLoading = false;
              });
          } else {
            this.$store
              .dispatch("shop/updatPlaceList", datas)
              .then((res) => {
                this.isLoading = false;
                this.$message({
                  message: res.msg,
                  type: res.code == 0 ? "success" : "error",
                });
                this.reloadData();
              })
              .catch((e) => {
                console.error(e);
                this.isLoading = false;
              });
          }
        }
      });
    },
    changeStatusClass(row, status) {
      let datas = {
        shop_id: Number(this.merchants.shop_id),
        merchant_id: Number(this.merchants.merchant_id),
        type_id: this.type_id,
        status: status ? 1 : 0,
      };
      this.isLoading = true;
      this.$store
        .dispatch("shop/updatePlaceType", datas)
        .then((res) => {
          this.$message({
            message: res.msg,
            type: res.code == 0 ? "success" : "error",
          });
          this.reloadData();
        })
        .catch((e) => {
          this.isLoading = false;
        });
    },
    //提交分类
    submitType() {
      this.$refs["typeForm"].validate((valid) => {
        if (valid) {
          this.typeForm.type_id = Number(this.typeForm.type_id);
          let datas = _.cloneDeep(this.typeForm);
          //datas.min_people_num = this.CalculateData(datas.min_people_num, "*");
          //datas.max_people_num = this.CalculateData(datas.max_people_num, "*");
          datas.basic_price = this.CalculateData(datas.basic_price, "*");
          datas.deposit = this.CalculateData(datas.deposit, "*");
          datas.shop_id = Number(this.merchants.shop_id);
          datas.merchant_id = Number(this.merchants.merchant_id);
          //datas.ename = datas.name;
          this.isLoading = true;
          if (datas.type_id == 0) {
            delete datas.type_id;
            this.$store
              .dispatch("shop/createPlaceType", datas)
              .then((res) => {
                this.$message({
                  message: res.msg,
                  type: res.code == 0 ? "success" : "error",
                });
                this.reloadData();
              })
              .catch((e) => {
                console.error(e);
                this.isLoading = false;
              });
          } else {
            this.$store
              .dispatch("shop/updatePlaceType", datas)
              .then((res) => {
                this.$message({
                  message: res.msg,
                  type: res.code == 0 ? "success" : "error",
                });
                this.reloadData();
              })
              .catch((e) => {
                console.error(e);
                this.isLoading = false;
              });
          }
        }
      });
    },
    //删除分类
    deleteType() {
      if (Number(this.typeForm.type_id) > 0) {
        this.$confirm("确定要删除吗？", "提示", {
          type: "warning",
        }).then(() => {
          let datas = {
            id: [Number(this.typeForm.type_id)],
            merchant_id: Number(this.merchants.merchant_id),
            shop_id: Number(this.merchants.shop_id),
          };
          this.$store.dispatch("shop/deletePlaceType", datas).then((res) => {
            this.$message({
              message: res.msg,
              type: res.code == 0 ? "success" : "error",
            });
            this.reloadData();
          });
        });
      }
    },
    //获得所有规则
    getAllRule() {
      let datas = {
        page_size: 1000000,
        merchant_id: Number(this.merchants.merchant_id),
        shop_id: Number(this.merchants.shop_id),
        status: 1,
      };
      this.$store.dispatch("shop/getPlaceRule", datas).then((res) => {
        this.placeRuleData = res.data;
      });
    },
    //翻页
    pageChange(val) {
      this.page = val;
      this.getAllPlace();
    },
    //重载规则数据
    reloadRule() {
      this.getAllRule();
    },
    //重载数据
    reloadData() {
      this.isLoading = false;
      this.placeDialog = false;
      this.placePlaceDialog = false;
      this.getPlaceType(() => {
        this.getAllPlace();
        this.getAllRule();
      });
    },
    getPlaceAreaData(data) {
      this.placeAreaData = data;
    },
    //切换回收站
    handleDeleteBox(v) {
      this.is_delete = v;
      this.reloadData();
    },
    deletePlace(items) {
      this.multipleSelection = items.list;
      if (items.isDelete) {
        this.handleDeletePlace();
      }
    },
    handleDeletePlace() {
      if (this.multipleSelection.length > 0) {
        this.$confirm("确定删除吗？", "提示").then(() => {
          let datas = {
            id: this.multipleSelection,
            merchant_id: Number(this.merchants.merchant_id),
            shop_id: Number(this.merchants.shop_id),
          };
          this.$store.dispatch("shop/deletePlaceList", datas).then((res) => {
            this.multipleSelection = [];
            this.getAllPlace();
          });
        });
      }
    },

    recoveryPlace(items) {
      this.multipleSelection = items.list;
      if (items.isRecovery) {
        this.handleRecoveryPlace();
      }
    },
    handleRecoveryPlace() {
      if (this.multipleSelection.length > 0) {
        this.$confirm("确定还原吗？", "提示").then(() => {
          let datas = {
            id: this.multipleSelection,
            merchant_id: Number(this.merchants.merchant_id),
            shop_id: Number(this.merchants.shop_id),
          };
          this.$store.dispatch("shop/recoveryPlaceList", datas).then((res) => {
            this.multipleSelection = [];
            this.getAllPlace();
          });
        });
      }
    },
    sortPlace(obj) {
      this.sort = obj.order ? obj.prop : "";
      this.order = obj.order ? this.sortableList[obj.order] : "";
      this.page = 1;
      this.getAllPlace();
    },
  },
  watch: {
    enable: {
      immediate: true,
      handler(val) {
        if (val) {
          this.reloadData();
        }
      },
    },
  },
  created() {},
};
</script>

